import { Button, Grid, Typography } from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import { api } from 'api';
import { ReactComponent as Email } from "assets/icons/email.svg";
import { ReactComponent as Key } from "assets/icons/key.svg";
import browserHistory from "browser-history/browser-history";
import { BonfireField } from "components/BonfireField/BonfireField";
import { useAuth } from 'context/AuthContext';
import { useNotifications } from 'context/NotificationsContext';
import { Form, Formik } from 'formik';
import React from 'react';
import * as Yup from 'yup';
import { ReactComponent as Google } from '../../img/google.svg';
import { useStyles } from './RegisterModal.style';

// const regexPassword = new RegExp(/^(?=.*\d)(?!.*\s).{6,15}$/);

const LoginValidate = Yup.object().shape({
    email: Yup.string().email('Format düzgün deyil'),
    password: Yup.string()
    // .matches(
    //     regexPassword,
    //     'Ən az 6 simvoldan ibarət olmalıdır',
    // ),
});


const loginInitials = {
    email: '',
    password: ''
}

//                      interface
interface Props {
    open: boolean;
    handleClose: () => void;
    className?: any | undefined;
    handleOpen: () => void;
    handleRegOpen: () => void;
}


interface GoogleButtonProps {
    onClick: () => void;
}

// ------------------------  components


const GoogleButton: React.FC<GoogleButtonProps> = ({ onClick }) => {
    const classes = useStyles();
    return (
        <Button type="submit" onClick={onClick} className={classes.googleBtn}>
            <Google className={classes.google} />
            <span className={classes.googleWord}>{"Sign in with google".toUpperCase()}</span>
        </Button>
    )
}


export const LoginModal: React.FC<Props> = ({ open, handleClose, className, handleRegOpen }) => {
    const classes = useStyles()
    const { saveAuthData } = useAuth()
    const { notify } = useNotifications()

    const loginOnSubmit = async (formData): Promise<void> => {
        try {
            const axios = await api();
            const res = await axios.post('user/login', formData)
            const accessToken = res.data.output.accessToken;
            saveAuthData({ accessToken, user: res.data.output });
            notify({
                type: 'success',
                message: `Xoş gəldiniz, ${res.data.output.name}`,
            });
            handleClose()
        } catch (error) {
            notify({
                type: 'error',
                message: 'Şifrəni və ya emaili səhv yazdınız',
            });
        }
    }
    // const googleLogIn = async () => {
    //     try {
    //         const axios = await api("https://bonfire-az.herokuapp.com/");
    //         const res = await axios.get('oauth2/authorization/google')
    //         console.log("res", res)
    //     } catch (e) {
    //         console.log(e);
    //     }
    // }
    return (
        <>
            <Dialog
                onClose={handleClose}
                classes={{
                    paper: className,
                }}
                open={open}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
                className={classes.dialog}
            >
                <Grid container className={classes.modal}>
                    <Grid item container xs={12}>
                        <Grid item xs={12}>
                            <Typography className={classes.leftHeader} style={{ marginBottom: '20px' }}>Log in to Bonfire</Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <GoogleButton onClick={() => { }} />
                        </Grid>
                    </Grid>
                    <Grid item container xs={12} className={classes.rightPart}>
                        <Grid item xs={12} className={classes.fieldCoContainer}>
                            <Grid item xs={12} className={classes.or}>
                                <Typography variant="h5" className={classes.orText}> <span>Or</span> <span className="span2"></span></Typography>
                            </Grid>
                            <Formik
                                enableReinitialize
                                initialValues={loginInitials}
                                validationSchema={LoginValidate}
                                onSubmit={(formData) => {
                                    loginOnSubmit(formData)
                                }}
                            >
                                {({ submitForm, values }): React.ReactNode => {
                                    return (
                                        <Form className={classes.fieldContainer} style={{ justifyContent: "space-between" }}>
                                            <BonfireField
                                                // className={classes.input}
                                                name="email"
                                                type="email"
                                                placeholder="email@example.com"
                                                inputIcon={<Email />}
                                                value={values.email}
                                            />
                                            <BonfireField
                                                // className={classes.input}
                                                name="password"
                                                type="password"
                                                value={values.password}
                                                placeholder="Password"
                                                inputIcon={<Key />}
                                            />

                                            <Typography className={classes.forgot} variant="h5" style={{ width: "fit-content" }} onClick={() => browserHistory.push("forgot-password")}>Forgot password?</Typography>


                                            <button type="submit" className={`btn-primary btn-sm ${classes.createAccount}`} style={{ marginTop: "20px", marginBottom: "20px" }}
                                                onClick={(e) => {
                                                    e.preventDefault()
                                                    submitForm()
                                                    e.preventDefault()
                                                }}>Log in</button>
                                            <Typography variant="h5" style={{ marginTop: "30px !important", textAlign: "center" }}> {"Don’t have an account?"}<span className={classes.blue} onClick={() => {
                                                handleClose()
                                                handleRegOpen()
                                            }}>{"Sign up for free"}</span></Typography>
                                        </Form>
                                    )
                                }
                                }
                            </Formik>
                        </Grid>
                    </Grid>
                </Grid>
            </Dialog>
        </>
    );
};
