import React, { useEffect, useState } from "react";
import logo from "../img/logo.svg";
import arrow_down from "../img/arrow_down.svg";
import gift from "../img/gift.svg";
import { images } from "../img/images";
import { dropIcons } from 'assets/icons/images'
import { Link } from "react-router-dom";
import Cart from "./Cart";
import { LoginModal, RegisterModal } from "./Auth";
import { useAuth } from "context/AuthContext";
import { ReactComponent as IconMoney } from '../img/iconMoney.svg'
import { CreateStoreModal } from '../components/CreateYourStore/components/Modal';

const exploreLeft = ["Staff Picks", "Adoption", "Animal Rescue", "Art & Illustrations", "Black Lives Matter", "Business", "Conversation", "COVID-19 Aid", "Creators", "Entertainment", "Events", "Fundraising", "Hobbies", "Holidays", "Medical", "Nonprofits", "Sports"]


const DropDownItem = ({ header, iconSrc, link }) => {
  return (
    <Link to={link}>
      <div className="sub-link">
        <div className="sub-link__icon__container">
          <img src={iconSrc} className="sub-link__icon" />
        </div>
        <h2 className="sub-link__title">{header}</h2>
        <img
          className="sub-link__arrow"
          src={images.swipeRight}
          alt="right arrow"
        />
      </div>
    </Link>
  )
}


const Menu = () => {
  const [openCreateStoreModal, setOpenCreateStoreModal] = useState(false);
  return (
    <>
      <CreateStoreModal
        open={openCreateStoreModal}
        onClose={() => {
          setOpenCreateStoreModal(false)
        }}
      />
      <nav className="menu">
        <ul className="menu__list">
          <li className="menu__item">
            Sell online
          <img src={arrow_down} alt="arrow down" />
            <div className="submenu">
              <div className="submenu-list">
                <DropDownItem
                  header="Sell online in a campaign"
                  link="/sell-online"
                  iconSrc={dropIcons.trade}
                />
                <DropDownItem
                  header="Create a merch store"
                  link="/create-store"
                  iconSrc={dropIcons.store}
                />
              </div>
            </div>
          </li>
          <li className="menu__item">
            <a href="/">Fundraise</a>
            <img src={arrow_down} alt="arrow down" />
            <div className="submenu">
              <div className="submenu-list">
                <DropDownItem
                  link=""
                  header="For individuals"
                  iconSrc={dropIcons.fundraiser}
                />
              </div>
            </div>
          </li>
          <li className="menu__item">
            <a href="/">Customize</a>
            <img src={arrow_down} alt="arrow down" />
            <div className="submenu">
              <div className="submenu-list">
                <DropDownItem
                  header="Order custom shirts"
                  link="/catalog"
                  iconSrc={dropIcons.shopping}
                />
                <DropDownItem
                  link="/templates"
                  header="Product catalog"
                  iconSrc={dropIcons.catalogue}
                />
                <DropDownItem
                  link="/where?"
                  header="Design templates"
                  iconSrc={dropIcons.edit}
                />
              </div>
            </div>
          </li>
          <li className="menu__item">
            <a href="/">Explore</a>
            <img src={arrow_down} alt="arrow down" />
            <div className="submenu">
              <div className="submenu-list">
                <DropDownItem
                  link="/store"
                  header="Store"
                  iconSrc={dropIcons.store}
                />
                <div>
                  <p style={{
                    padding: "20px 0px 0px",

                  }}>BY CATEGORY</p>
                  <div style={{
                    columnCount: 2,
                    columnRuleStyle: "solid",
                    columnRuleColor: "#F2F4F8",
                    columnGap: "40px"
                  }}>
                    {
                      exploreLeft.map((txt, index) => <p style={{ color: "#6581A3", }} key={index}>{txt}</p>)
                    }
                  </div>
                </div>
              </div>
            </div>
          </li>
          <li className="menu__item">
            <img src={gift} alt="gift icon" className="gift-icon" />
            <a href="/">Gifts</a>
            <img src={arrow_down} alt="arrow down" />
          </li>
        </ul>
      </nav>
    </>
  );
};
const AuthButtons = (setOpenLoginModal, setOpenRegModal) => {
  return (
    <>
      <button className="header__login-btn btn-frame btn-sm"
        onClick={() => {
          setOpenLoginModal(true)
        }} >
        log in
      </button>
      <button className="btn-primary btn-sm"
        onClick={() => {
          setOpenRegModal(true)
        }} >
        get started
      </button>
    </>
  );
};
const UserButtons = (toggleCart) => {
  const { user, removeAuthData } = useAuth()
  return (
    <>
      <div className="user menu__item">
        <img className="user__img" src={images.user} alt="user picture" />
        <h4 className="user__name">{user?.fullName.split(" ")[0]}</h4>
        <img src={arrow_down} />
        <div className="submenu" style={{ left: "-149px", width: "210px" }}>
          <div className="submenu-list" style={{ padding: "24px 0px 0px" }} >
            <div >
              <h2 className="user_profile_header">Welcome, {user?.fullName.split(" ")[0]} !</h2>
              <a href="/dashboard" className="user_profile_href">Dashboard</a>
              <a href="/dashboard/account" className="user_profile_href">Profile Settings</a>
              <a href="/forgot-password" className="user_profile_href">Change Password</a>
              <a href="" className="user_profile_href">Email Preferences</a>
              <p className="user_profile_href" style={{ marginBottom: "0px" }} onClick={() => {
                removeAuthData()
              }}>Log out</p>
            </div>
          </div>
        </div>
      </div>
      <div className="cart-btn" onClick={toggleCart}>
        <img src={images.cart} alt="user picture" />
        <h4 className="user__name">Cart</h4>
      </div>
    </>
  );
};

const Header = () => {
  const { user } = useAuth()
  const [isLoggedIn, setIsLoggedIn] = useState(user !== null);
  const [isCartActive, setIsCartActive] = useState(false);
  const [openRegModal, setOpenRegModal] = useState(false);
  const [openLoginModal, setOpenLoginModal] = useState(false);

  useEffect(() => {
    setIsLoggedIn(user !== null)
  }, user)

  const toggleCart = () => {
    setIsCartActive(!isCartActive);
  };
  useEffect(() => {
    window.onscroll = () => {
      if (window.pageYOffset > 30) {
        if (document.querySelector(".header"))
          document.querySelector(".header").classList.add("header--minimized");
      }
      if (window.pageYOffset < 30) {
        if (document.querySelector(".header"))
          document
            .querySelector(".header")
            .classList.remove("header--minimized");
      }
    };
  }, []);

  return (
    <>

      <RegisterModal
        open={openRegModal}
        handleClose={() => setOpenRegModal(false)}
        handleOpen={() => setOpenRegModal(true)}
        handleLoginOpen={() => setOpenLoginModal(true)}
      />
      <LoginModal
        open={openLoginModal}
        handleClose={() => setOpenLoginModal(false)}
        handleOpen={() => setOpenLoginModal(true)}
        handleRegOpen={() => setOpenRegModal(true)}
      />
      <header className="header">
        <div className="container">
          <div className="header__inner">
            <Link to="/">
              <img src={logo} alt="logo" />
            </Link>
            {Menu()}
            {isLoggedIn ? UserButtons(toggleCart) : AuthButtons(setOpenLoginModal, setOpenRegModal)}
            <Cart isCartActive={isCartActive} toggleCart={toggleCart} />
          </div>
        </div>
      </header>
    </>
  );
}
export default Header;
