import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles((theme) => ({
    dialog: {
        minWidth: '700px',
        minHeight: '110vh'
    },
    modal: {
        minHeight: '50vh',
        width: '500px',
        padding: `${theme.spacing(5)}px ${theme.spacing(5)}px`,
        overflowY: 'scroll',
        '&::-webkit-scrollbar': {
            display: 'none'
        }
    },
    forgot: {
        cursor: 'pointer'
    },
    blue: {
        color: "#1877F2",
        cursor: 'pointer',
        marginLeft: "10px"
    },
    checkBox: {
        "& .MuiSvgIcon-root": {
            width: "19px !important",
            height: "19px !important",
        }
    },
    googleBtn: {
        minWidth: '200px',
        width: "100%",
        display: 'flex',
        justifyContent: 'space-between',
        textTransform: 'unset',
        borderRadius: '7px',
        boxShadow: '2px 2px 16px #e7ecf1, inset 0 0 0 1px #c9d5e1'
    },
    google: {
        width: '20px',
        height: '20px',
    },
    googleWord: {
        fontSize: 14,
        fontWeight: 500,
        flex: 1
    },
    leftHeader: {
        fontWeight: 600,
        fontSize: 20,
    },
    signIn: {
        fontWeight: 600,
        fontSize: '13px'
    },
    signInCont: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        height: '160px',
    },
    hr: {
        height: '2px',
        opacity: '.2',
        maxWidth: '200px'
    },
    // google button
    // facebook Button
    fcCont: {
        '& .MuiButton-root:hover': {
            backgroundColor: '#2064bd'
        }
    },
    facebookBtn: {
        minWidth: '200px',
        display: 'flex',
        justifyContent: 'space-between',
        textTransform: 'unset',
        borderRadius: '6px',
        backgroundColor: '#1877f2',
    },
    facebook: {
        width: '20px',
        height: '20px',
    },
    facebookWord: {
        fontWeight: 600,
        fontSize: 15,
        flex: 1,
        color: 'white'
    },
    blueLink: {
        color: '#5e82a6',
        "&:hover": {
            color: '#69aaec',
            textDecoration: 'none'
        },
        marginTop: '10px'
    },
    haveAccount: {
        marginTop: '20px',

    },
    // rightPart
    rightPart: {

    },
    rightHeader: {
        marginTop: '50px',
    },
    or: {
        display: "flex",
        justifyContent: "center",
        position: "relative",
        height: 60,
    },
    orText: {
        fontSize: "30px",
        width: "100%",
        textAlign: "center",
        position: "relative",
        "& span": {
            position: "absolute",
            left: 193,
            zIndex: 1,
            backgroundColor: "white"
        },
        '& .span2': {
            border: "1px solid #ccc",
            textDecoration: "line-through",
            width: "100%",
            height: 1,
            position: "absolute",
            top: 22,
            left: 0,
            zIndex: 0,
            backgroundColor: "#ccc"
        }
    },
    fieldContainer: {
        minHeight: '260px',
        display: 'flex',
        alignItems: 'space-between',
        flexDirection: 'column',
        justifyContent: 'space-between'
    },
    fieldCoContainer: {
        marginTop: '20px',
    },
    fieldLabel: {
        fontWeight: 600
    },
    field: {
        '& .MuiOutlinedInput-inputMarginDense': {
            color: '#3d4853',
            lineHeight: 1.6,
            padding: ' 12px 16px',
            fontWeight: 800,
            fontSize: '1.5rem',
        },
        '& .MuiFormHelperText-root.Mui-error': {
            fontSize: '13px'
        }
    },
    createAccountCont: {
        display: 'flex',
        justifyContent: 'center',
    },
    createAccount: {
        color: 'white',
        fontSize: '14px',
        fontWeight: 600,
        backgroundColor: '#35CF94',
        [theme.breakpoints.only('lg')]: {
            width: '100%'
        }
    }
}))
