import { Link } from "@material-ui/core";
import React, { useState } from "react";
import { images } from "../img/images";

const Cart = ({ isCartActive, toggleCart }) => {
  const [products, setProducts] = useState([1, 2, 3]);
  const removeProduct = (index) => {
    let newArray = [...products];
    newArray.splice(index, 1);
    setProducts(newArray);
  };
  return (
    <div className={`cart ${isCartActive ? "cart--active" : ""}`}>
      <div className="cart__wrapper" onClick={toggleCart} />
      <div className="cart__inner">
        <div className="cart__cancel-btn" onClick={toggleCart}>
          &times;
        </div>
        <h2 className="heading-2">Your cart</h2>
        {products.length === 0 ? (
          <h3 className="heading-3">Your cart is empty</h3>
        ) : (
          <ul className="cart-list">
            {products.map((p, i) => (
              <li className="cart-list-item" key={i}>
                <div className="element">
                  <img src={images.product} alt="product" />
                  <div className="qty">
                    <h3 className="heading-3">QTY</h3>
                    <input type="text" />
                  </div>
                  <div className="prd">
                    <h3 className="heading-3">product</h3>
                    <p>Classic Long Sleeve</p>
                  </div>
                  <div className="size">
                    <h3 className="heading-3">Size</h3>
                    <select>
                      <option defaultValue value="M">
                        M
                      </option>
                      <option value="S">S</option>
                      <option value="XL">XL</option>
                      <option value="XXL">XXL</option>
                    </select>
                  </div>
                  <div className="price">
                    <h3 className="heading-3">price</h3>
                    <p>$27.99</p>
                  </div>
                  <div className="element__delete-btn" onClick={() => removeProduct({ i })}>
                    &times;
                  </div>
                </div>
              </li>
            ))}
          </ul>
        )}
        <Link className="cart__add-another-item">+ Add another item</Link>
      </div>
    </div>
  );
};
export default Cart;
