import { Dialog, Grid, makeStyles, Typography } from '@material-ui/core';
import { api } from 'api';
import { BonfireField } from 'components/BonfireField/BonfireField';
import { Form, Formik } from 'formik';
import React from 'react';
import storeCart from '../../../img/store-cart.png';


const useStyles = makeStyles(() => ({
    header: {
        fontSize: 25,
        color: '#35CF94',
        fontWeight: 600,
        textAlign: 'center',
        width: "100%"
    },
    inputLabel: {

    },
    rightPart: {
        padding: '40px 20px'
    },
    normalTxt: {
        fontSize: 13
    }
}))

export const CreateStoreModal = ({ open, onClose, }) => {
    const initialValues = {
        name: ''
    }
    const onSubmit = async () => {
        try {
            const axios = await api()
            const res = await axios.get(`/stores/$'sil'{userId}`)
            console.log(res.data)
        } catch (e) {
            console.log(e);
        }
    }

    const handleClose = () => {
        onClose(true)
    }
    const classes = useStyles()
    return (
        <Formik
            initialValues={initialValues}
            onSubmit={onSubmit}
        >
            {({ submitForm }): React.ReactNode => {
                return (
                    <Form>
                        <Dialog
                            open={open}
                            onClose={handleClose}
                            aria-labelledby="simple-modal-title"
                            aria-describedby="simple-modal-description"
                            style={{ borderRadius: 0, }}
                        >
                            <Grid container>
                                <Grid item xs={6}>
                                    <img src={storeCart} alt="" />
                                </Grid>

                                <Grid item container xs={6} className={classes.rightPart}>
                                    <Typography className={classes.header}>Create your store</Typography>
                                    <Grid item lg={12}>
                                    </Grid>
                                    <BonfireField
                                        name="name"
                                        placeholder="For example - My Red Store "
                                    />
                                    <Grid item lg={12}>
                                        <Typography className={classes.normalTxt}>No worries :) You can change it later.</Typography>
                                    </Grid>
                                    <Grid item lg={12} style={{ display: 'flex', justifyContent: 'center' }}>
                                        <a onClick={submitForm} href="#" className="btn-primary header__get-started-btn" style={{ maxWidth: 150, maxHeight: 40, padding: "1.2rem 2.4rem", marginTop: 20 }}>
                                            Create
                                        </a>
                                    </Grid>
                                </Grid>
                            </Grid >
                        </Dialog >
                    </Form>
                )
            }}
        </Formik>
    )
}