import React from 'react';
import logo_2 from '../img/logo_2.svg';
import { images } from '../img/images';

function Footer() {
    return (
        <footer className="footer">
            <div className="container">
                <div className="footer__inner">
                    <div className="footer__block">
                        <img src={logo_2} alt="logo" />
                        <div className="footer__slogan">Where the world goes for premium custom shirts</div>
                        <div className="footer__copyright">
                            <div className="footer__copyright-text">© 2020 Bonfire.com</div>
                            <a className="footer__dmca" href="/">
                                DMCA
                            </a>
                            <a className="footer__trademark" href="/">
                                Trademark Takedown
                            </a>
                        </div>
                    </div>
                    <div className="footer__block">
                        <h4 className="footer__block-title">using bonfire</h4>
                        <ul>
                            <li className="footer__item">
                                <a className="footer__link" href="/sell-online">
                                    Sell shirts online
                                </a>
                            </li>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Start a fundraiser
                                </a>
                            </li>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Order custom shirts
                                </a>
                            </li>
                            <li className="footer__item ">
                                <a className="footer__link footer__giftBtn" href="/">
                                    Shop gifts
                                </a>
                            </li>
                        </ul>
                        <a className="btn-tertiary btn-sm footer__campaign-btn" href="/">
                            start a campaign
                        </a>
                    </div>
                    <div className="footer__block">
                        <h4 className="footer__block-title">resources</h4>
                        <ul>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Help center
                                </a>
                            </li>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Design templates
                                </a>
                            </li>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Request a custom design
                                </a>
                            </li>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Product catalog
                                </a>
                            </li>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Pricing calculator
                                </a>
                            </li>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Blog
                                </a>
                            </li>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Stories
                                </a>
                            </li>
                        </ul>
                    </div>
                    <div className="footer__block">
                        <h4 className="footer__block-title">company</h4>
                        <ul>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    About
                                </a>
                            </li>
                            <li className="footer__item footer__item--highlighted">
                                <a className="footer__link" href="/">
                                    <strong>Careers</strong>
                                    Reviews
                                </a>
                            </li>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Contact
                                </a>
                            </li>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Privacy
                                </a>
                            </li>
                            <li className="footer__item">
                                <a className="footer__link" href="/">
                                    Terms
                                </a>
                            </li>
                        </ul>
                    </div>
                    <div className="footer__block">
                        <h4 className="footer__block-title">follow us</h4>
                        <ul className="footer__social-list">
                            <li>
                                <a href="/">
                                    <img src={images.facebook} alt="facebook icon" />
                                </a>
                            </li>
                            <li>
                                <a href="/">
                                    <img src={images.instagram} alt="instagram icon" />
                                </a>
                            </li>
                            <li>
                                <a href="/">
                                    <img src={images.pinterest} alt="pinterest icon" />
                                </a>
                            </li>
                            <li>
                                <a href="/">
                                    <img src={images.twitter} alt="twitter icon" />
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </footer>
    );
}
export default Footer;
